import React, {Component} from "react";
import axios from 'axios'
import Qs from "qs"
import "./login.css"
import {Link} from "react-router-dom";

class Login extends Component{
    state = {
        mobile:"",
        password:"",
        access_key: 'ad1012333204405c50b9ed44883d3c0c',   // 公共参数
    }

    handleInputMobile = (e) => {
        this.setState({mobile:e.target.value})
    }

    handlePassword = (e) => {
        this.setState({password:e.target.value})
    }

    handleClick=(state)=>{
        // console.log(this.state)
        axios.post('https://www.it266.com/api/customer/token?access_key='+this.state.access_key,Qs.stringify(this.state))
            .then((response)=>{
                // console.log(response)
                if(response.data.status){
                    window.localStorage.setItem("token",response.data.data.token)
                    alert("登录成功")
                    window.location = "http://localhost:3000/#/Userinfo"
                }else{
                    document.getElementsByClassName("message")[0].innerHTML = response.data.data
                }
            })
            .catch((error)=>{
                console.log(error);
        })
    }

    render() {
        return(
            <div className={"Login"} >
                <h2 className="log">登录</h2>
                <div className="weui-cells weui-cells_form">
                    <div className="weui-cell weui-cell_active lines">
                        <div className="weui-cell__hd"><label className="weui-label">手机号</label></div>
                        <div className="weui-cell__bd">
                            <input className="weui-input js_input" placeholder="请输入手机号"
                                   value={this.state.username}
                                   onChange={this.handleInputMobile}

                            />
                        </div>
                    </div>

                    <div className="weui-cell weui-cell_active lines">
                        <div className="weui-cell__hd"><label className="weui-label">密&nbsp;&nbsp;码</label></div>
                        <div className="weui-cell__bd">
                            <input className="weui-input js_input" type="password"
                                   value={this.state.password}
                                   onChange={this.handlePassword}
                                   placeholder="请输入密码" />
                        </div>
                    </div>

                    <div className="weui-flex">
                        <div className="weui-flex__item"><div className="placeholder message"></div></div>
                        <div className="weui-flex__item"><div className="placeholder message_right">
                            没有账号?请往<Link to="/Resgister">注册</Link>
                        </div></div>
                    </div>

                    <div className="button-sp-area cell">
                        <div className="weui-btn_cell weui-btn_cell-default"
                               id="showTooltips"
                                onClick={this.handleClick}
                                to={"/Userinfo"}
                            >登录</div>
                    </div>

                </div>

            </div>
        );
    }
}


export default Login

