import React, {Component} from 'react'
import axios from 'axios'
import qs from 'qs'
import {Link} from "react-router-dom";

import './todo.css'
import "weui"
import "react-weui"
import "layui-laydate"
import flag from '../../../img/小于号.png'

class Create extends Component{

    state={
        token:window.localStorage.getItem("token"),
        name:'',
        remind_at:'',
        repeat:'',
        status:''
    }


    handleClick = () => {
        console.log(this.state)
        let token=window.localStorage.getItem("token")
        axios.post('https://www.it266.com/app/todo/create?token='+this.state.token, qs.stringify(this.state))
        .then((response)=>{
            console.log(response)
            if(response.data.status){
                this.props.history.push("/Todo")
            }else{
                alert(response.data.data)
            }
        })

    }

    render() {

        return (
            <div className='todo_Create'>

                <div className="weui-flex one lines">
                    <span><Link to={"/Todo"}><img className="flag" src={flag}/></Link></span>
                    <div className="weui-flex__item"><div className="placeholder">新增事项</div></div>
                    <div><div className="placeholder" onClick={this.handleClick}>完成</div></div>
                </div>

                <div className="weui-panel two">
                    <div className="weui-panel__hd lines"></div>

                    <div className="weui-panel__bd">
                        <div className="weui-media-box weui-media-box_small-appmsg">
                            <div className="weui-cells lines">
                                <a className="weui-cell weui-cell_active weui-cell_access weui-cell_example line">
                                    <input type="text" placeholder="标题" value={this.state.name} onChange={(e) =>{
                                        this.setState({name:e.target.value})
                                     }}
                                    />
                                </a>
                                <a className="weui-cell weui-cell_active weui-cell_access weui-cell_example">
                                    <input type="text" placeholder="备注" />
                                </a>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="three">
                    <div className="weui-panel__hd lines"></div>

                    <div className="weui-cells weui-cells_form">
                        <div className="weui-cell weui-cell_switch line">
                            <div className="weui-cell__bd three_left1">在指定日期提醒我</div>
                            <div className="weui-cell__ft">
                            <div>
                                <input type="checkbox" className="weui-switch"/></div></div>
                        </div>
                    </div>

                    <div className="weui-cells">
                        <div className="weui-cell weui-cell_active weui-cell_access line">
                            <div className="weui-cell__bd three_left2">闹钟</div>
                            <div className="weui-cell__bd "><div>
                                <label className="layui-form-label"></label>
                                <div className="layui-input-inline">
                                    <input type="text" className="layui-input" id="test5" placeholder="yyyy-MM-dd HH:mm:ss"
                                    value={this.state.remind_at} onChange={(e) => {
                                        this.setState({remind_at: e.target.value})
                                    }}
                                    />
                                </div>
                            </div>
                            </div>
                        </div>
                    </div>

                    <div className="weui-cells lines">
                        <div className="weui-cell weui-cell_active weui-cell_access">
                            <div className="weui-cell__bd">重复</div>
                            <div className="weui-cell__ft three_right">
                                 <select className="weui-select" onChange={(e)=>{this.setState({repeat:e.target.value})}}>
                                     <option value="" label="">重复</option>
                                     <option value="0" label="不重复">不重复</option>
                                     <option value="1" label="每天">每天</option>
                                     <option value="2" label="每周">每周</option>
                                     <option value="3" label="每月">每月</option>
                                     <option value="4" label="每季度">每季度</option>
                                     <option value="5" label="每年">每年</option>
                                 </select>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        )
    }
}
export default Create