import React, {Component} from 'react';
import 'weui';
import axios from 'axios'
import './resgister.css'
import {Link} from 'react-router-dom';
import qs from 'qs';

class Register extends Component {

    state={
        mobile:'',
        password:'',
        nickname:'',
        url:'',           // 验证码弹窗图片网址
        captcha_key:'',   // 图形验证码key
        isShow:'none',    // 图片验证码区域显示/隐藏
        captcha_code:'',  // 图形验证码内容
        access_key: 'ad1012333204405c50b9ed44883d3c0c',   // 公共参数
        isma:'none',     // 手机验证码区域显示/隐藏
        s:'60',           // 倒计时
        flag:'true',
        isfalse:'none',   // 图片验证码输入错误时显示
        verification_key:'', //短信验证码key
        verification_code:'',  // 短信验证码内容
    }

    // 判断手机号码格式是否正确
    isPhone=()=>{
        let res = this.state.mobile.match(/^1[0-9]{10}$/)
        if(res === null && this.state.mobile !== ''){
            return (
                <div className="weui-form__tips-area">
                    <p className="weui-form__tips" style={{color: 'red',marginRight:'40px',textAlign:'right'}}>
                        请输入正确的手机号码
                    </p>
                </div>
            )
        }
    }

    // 判断密码格式是否正确
    isPassword=()=>{
        let res = this.state.password.match(/.{6}/)
        if(res === null && this.state.password !== ''){
            return (
                <div className="weui-form__tips-area">
                    <p className="weui-form__tips" style={{color: 'red',marginRight:'40px',textAlign:'right'}}>
                        至少输入6位密码
                    </p>
                </div>
            )
        }
    }

    // 判断昵称格式是否正确
    isNickname=()=>{
        let res = this.state.nickname.match(/.{2}/)
        if(res === null && this.state.nickname !== ''){
            return (
                <div className="weui-form__tips-area">
                    <p className="weui-form__tips" style={{color: 'red',marginRight:'40px',textAlign:'right'}}>
                        至少输入2位以上昵称
                    </p>
                </div>
            )
        }
    }

    // 手机号码密码格式正确显示验证码弹窗
    isHidden=()=>{
        if(this.state.mobile.match(/^1[0-9]{10}$/) && (this.state.password.match(/.{6}/)) && (this.state.nickname.match(/.{2}/))){
            // ajax请求获取安全验证的图⽚
            axios.post('https://www.it266.com/api/image/captcha')
                .then( (response) => {
                    // console.log(response)
                    this.setState({url:response.data.data.url,captcha_key:response.data.data.captcha_key})  // 安全验证的图片网址,key
                    this.setState({isShow:'block',})
                })
        }
    }

    // 倒计时
    setTime=()=>{
        let count = 60
        this.time = setInterval(()=>{
            this.setState({s:count})
            count--
            if(count<=0){
                clearInterval(this.time)
                this.setState({s:'60'})
                count = 60
            }},1000)
    }

    componentWillUnmount() {
        clearInterval(this.time)
    }

    // 图片验证码信息
    getMessage=()=>{
        let data = {
            access_key:this.state.access_key,
            mobile:this.state.mobile,
            captcha_key:this.state.captcha_key,
            captcha_code:this.state.captcha_code
        }

        if(this.state.captcha_code){
            axios.post('https://www.it266.com/api/sms/verification',qs.stringify(data))
                .then( (response) => {
                    // console.log(response)
                    if(response.data.status === true){
                        this.setState({isShow:'none',isma:'block',verification_key:response.data.data.verificationKey})
                        // 计时器
                        {this.setTime()}
                        console.log(this.state.verification_key)
                        window.alert(response.data.data.message)
                    }else{
                        this.setState({isfalse:'block'})
                        axios.post('https://www.it266.com/api/image/captcha')
                            .then( (response) => {
                                // console.log(response)
                                // 安全验证的图片网址,key
                                this.setState({url:response.data.data.url,captcha_key:response.data.data.captcha_key})
                                this.setState({isShow:'block',})
                            })
                    }
                })
                .catch(function (error) {
                    console.log(error);
                });
        }
    }

    // 手机短信验证码注册
    istrue=()=>{
        let data = {
            mobile:this.state.mobile,
            password:this.state.password,
            nickname:this.state.nickname,
            verification_key:this.state.verification_key,
            verification_code:this.state.verification_code
        }

        if(this.state.verification_code !== null){
            axios.post('https://www.it266.com/api/customer/create?access_key='+this.state.access_key,qs.stringify(data))
                .then((response) =>{
                    console.log(111)
                    console.log(response)
                    window.localStorage.setItem('token',response.data.data.token)
                    window.alert(response.data.data.message)
                })
                .catch(function (error) {
                    console.log(error);
                });
        }else{
            window.alert('请输入验证码')
        }
    }

    render() {
        return (
            <div className={'Register'}>
                <div className="weui-form">
                    <div className="weui-form__text-area">
                        <h2 className="weui-form__title">注册账号</h2>
                    </div>
                    <div className="weui-form__control-area">
                        <div className="weui-cells__group weui-cells__group_form">
                            <div className="weui-cells weui-cells_form">
                                <div className="weui-cell weui-cell_active">
                                    <div className="weui-cell__hd"><label className="weui-label">手机号码</label></div>
                                    <div className="weui-cell__bd">
                                        <input id="js_input"
                                               className="weui-input"
                                               placeholder="请输入您的手机号码"
                                               onChange={(e)=>{
                                                   this.setState({mobile: e.target.value})
                                               }}
                                        />
                                    </div>
                                </div>
                                {this.isPhone()}
                                <div className="weui-cell weui-cell_active">
                                    <div className="weui-cell__hd"><label className="weui-label">密码</label></div>
                                    <div className="weui-cell__bd">
                                        <input id="js_input"
                                               type='password'
                                               className="weui-input"
                                               placeholder="请输入密码"
                                               onChange={(e)=>{
                                                   this.setState({password: e.target.value})
                                               }}
                                        />
                                    </div>
                                </div>
                                {this.isPassword()}
                                <div className="weui-cell weui-cell_active">
                                    <div className="weui-cell__hd"><label className="weui-label">昵称</label></div>
                                    <div className="weui-cell__bd">
                                        <input id="js_input"
                                               className="weui-input"
                                               placeholder="请输入昵称"
                                               onChange={(e)=>{
                                                   this.setState({nickname: e.target.value})
                                               }}
                                        />
                                    </div>
                                </div>
                                {this.isNickname()}
                            </div>
                        </div>
                    </div>
                </div>

                <span className={'gologin'}>
                    已有账号，<Link to={'/Login'} className={'gologintext'}>点击登录</Link>
                </span>
                <div className="weui-form__opr-area">
                    <a className="weui-btn weui-btn_primary"
                       id="showTooltips"
                       onClick={this.isHidden}
                    >确定</a>
                </div>

                <div style={{display:this.state.isShow}} className={'isshow'}>
                    <span className={'isshow-span1'}>验证码:</span><img src={this.state.url} className={'isshow-img'}/>
                    <div className="weui-cell weui-cell_active weui-cell_active1">
                        <div className="weui-cell__hd"><label className="weui-label">图片验证码</label></div>
                        <div className="weui-cell__bd">
                            <input id="js_input"
                                   className="weui-input"
                                   placeholder="请输入图片验证码"
                                   onChange={(e)=>{
                                       this.setState({captcha_code: e.target.value})
                                       this.setState({isfalse:'none'})
                                   }}
                            />
                        </div>
                    </div>
                    <div className="weui-cell weui-cell_active"></div>
                    <div className={'isshow-false'} style={{display:this.state.isfalse}}>请输入正确的图片验证码</div>
                    <div className="weui-form__opr-area">
                        <a className="weui-btn weui-btn_primary"
                           id="showTooltips"
                           onClick={this.getMessage}
                        >获取手机验证码</a>
                    </div>
                </div>
                <div className={'ma'}  style={{display:this.state.isma}}>
                    <div className="weui-form__control-area ma1">
                        <div className="weui-cells__group weui-cells__group_form">
                            <div className="weui-cells weui-cells_form ma2">
                                <div className="weui-cell weui-cell_active">
                                    <div className="weui-cell__hd"><label className="weui-label">手机号</label></div>
                                    <div className="weui-cell__bd">
                                        <input className="weui-input" type="number" pattern="[0-9]*"
                                               placeholder="请输入手机号" value={this.state.mobile} readOnly/>
                                    </div>
                                    <div className="weui-cell__ft">
                                        <a className="weui-btn_reset weui-btn_icon">
                                            <i id="showIOSDialog1" className="weui-icon-info-circle"></i>
                                        </a>
                                    </div>
                                </div>
                                <div className="weui-cell weui-cell_active weui-cell_vcode">
                                    <div className="weui-cell__hd"><label className="weui-label">验证码</label></div>
                                    <div className="weui-cell__bd">
                                        <input autoFocus className="weui-input"
                                               type="text"
                                               id="js_input"
                                               placeholder="输入验证码"
                                               maxLength="6"
                                               onChange={(e)=>{
                                                   this.setState({verification_code:e.target.value})
                                               }}
                                        />
                                    </div>
                                    <div className="weui-cell__ft">
                                        <button className="weui-btn weui-btn_default weui-vcode-btn">{this.state.s}</button>
                                    </div>
                                </div>
                            </div>
                            <div className="weui-cells__tips"><a className="weui-link" >收不到验证码?</a>
                            </div>
                        </div>
                    </div>

                    <div className="weui-form__tips-area">
                        <label id="weuiAgree" htmlFor="weuiAgreeCheckbox" className="weui-agree">
                            <input id="weuiAgreeCheckbox" type="checkbox" className="weui-agree__checkbox"/><span
                            className="weui-agree__text">阅读并同意<a>《相关条款》</a></span>
                        </label>
                    </div>
                    <div className="weui-form__opr-area">
                        <Link className="weui-btn weui-btn_primary"
                              id="showTooltips"
                              onClick={this.istrue}
                              to={'/Login'}
                        >确定</Link>
                    </div>
                </div>
            </div>
        );
    }
}

export default Register;