import React, {Component} from 'react'
import {Link} from "react-router-dom";
import "weui"
import axios from 'axios'
import qs from 'qs';
import rili from "../../img/rili.png"
import plan from "../../img/plan.png"
import quanbu from "../../img/quanbu.png"
import flag from "../../img/小于号.png"

import "./data.css"

class Data extends Component{
    state={
        token:window.localStorage.getItem("token"),
        pagelist:[],
        page:'',        //当前页码 默认1
        item_count:'',  //总计量数
        page_size:'',   //每页记录数
        datalist:[],
        id:'',
        name:'',
        status: '',     //状态
        created_at:'',
        finish_at:'',
        remind_at: ''
    }

    componentDidMount(){
        this.getData()
    }

    getData =()=>{

        let token = window.localStorage.getItem("token")
        axios.get( 'https://www.it266.com/app/todo?token='+this.state.token,qs.stringify(this.state))
            .then((response)=>{
                // console.log(response)
                if(response.data.status){
                    this.setState({
                        dataList:response.data.data.data,
                        item_count:response.data.data.page.item_count,
                        page:response.data.data.page.page,
                        page_size:response.data.data.page.page_size,
                    })
                }
                else{
                    alert(response.data.data)
                }
            })
            .catch((error)=>{
                console.log(error)
            })
    }

    render() {
        return (
            <div className='Data'>
                <div className="weui-flex">
                     <div className="weui-flex__item">
                         <Link to={"/Userinfo"}><img className="flag" src={flag}/><span className="info">个人信息</span></Link>
                     </div>
                    <div className="weui-flex__item">
                        <Link to={"/Todo"}><div className="placeholder one_right">编辑</div></Link>
                    </div>
                </div>

                <div className="page__bd">
                    <div className="weui-search-bar" id="searchBar">
                        <form className="weui-search-bar__form">
                            <div className="weui-search-bar__box">
                                <i className="weui-icon-search"></i>
                                <input type="search" className="weui-search-bar__input" id="searchInput"
                                    placeholder="搜索" required
                                    onClick={()=>{
                                        this.getData()
                                    }}
                                />
                                <a className="weui-icon-clear" id="searchClear"></a>
                            </div>
                            <label className="weui-search-bar__label" id="searchText" >
                                <i className="weui-icon-search"></i>
                                <span>搜索</span>
                            </label>
                        </form>
                        <a className="weui-search-bar__cancel-btn" id="searchCancel">取消</a>
                    </div>
                </div>

                <div className="weui-flex three">
                    <div className="weui-flex__item">
                        <div className="placeholder">
                            <img className="rili" src={rili}  alt=""/>
                        </div>
                        <div className="placeholder">
                            今天
                        </div>
                    </div>
                    <div className="weui-flex__item">
                         <div className="placeholder">
                            <img className="plan" src={plan}  alt=""/>
                        </div>
                        <div className="placeholder">
                            计划
                        </div>
                    </div>
                </div>

                <div className="weui-flex three">
                    <div className="weui-flex__item">
                        <div className="placeholder">
                            <img className="quanbu" src={quanbu}  alt=""/>
                            <span>{this.state.item_count}</span>
                        </div>
                        <div className="placeholder">
                            全部
                        </div>
                    </div>
                </div>


            </div>

        )
    }
}
export default Data