import React, {Component} from 'react'
import axios from 'axios'
import qs from 'qs';
import flag from '../../img/小于号.png'
import flagb from '../../img/大于号.png'
import "./userinfo.css"
import {Link} from "react-router-dom";

class Userinfo extends Component{

    state={
        mobile:'',
        nickname:'',
        // access_key: 'ad1012333204405c50b9ed44883d3c0c',
        token:window.localStorage.getItem("token"),
    }

    componentDidMount(){
        axios.get('https://www.it266.com/api/customer/whoami?token='+this.state.token, qs.stringify(this.state))
        .then((response)=>{
            // console.log(response)
            this.setState({mobile:response.data.data.mobile})
            this.setState({nickname:response.data.data.nickname})
            // console.log(this.state)
        })
    }

    render() {
        return (

            <div className='userinfo'>
                <div className='header'>
                    <span><Link to={"/Data"}><img className="flag" src={flag}/></Link></span>
                    <span> <h2>个人信息</h2> </span>
                    <span></span>
                </div>

                {/*<div className="nav">*/}
                {/*    <Link to={"/"} >退出</Link>*/}
                {/*</div>*/}
                <div className="weui-flex nav ">
                    <div className="weui-flex__item"><div className="placeholder"></div></div>
                    <Link to={"/"} >退出</Link>
                </div>

                <div>
                    <div className="weui-flex lines">
                        <div className="weui-flex__item"><div className="placeholder">昵称</div></div>
                        <div className="weui-flex__item"><div className="placeholder">
                            <span>{this.state.nickname}</span><img className="flagb" src={flagb} />
                            </div></div>
                    </div>

                     <div className="weui-flex lines">
                        <div className="weui-flex__item"><div className="placeholder">手机号码</div></div>
                        <div className="weui-flex__item"><div className="placeholder">
                            <span>{this.state.mobile}</span><img className="flagb" src={flagb} />
                        </div></div>
                    </div>

                </div>
            </div>

        )
    }
}
export default Userinfo