import React, {Component} from 'react'

import "./index.css"
import {Link} from "react-router-dom";

class Index extends Component{

    render() {
        return(
            <div className={"sy"} >

                <div className="one">
                    <span className="weui-badge">初次见面</span>
                </div>


                <div className="weui-btn-area">
                    <Link className="link" to="/Resgister">
                        <button className="weui-btn weui-btn_plain-primary register">注册</button>
                    </Link>
                    <Link className="link" to="/Login">
                        <button className="weui-btn weui-btn_plain-primary login">登录</button>
                    </Link>
                </div>

            </div>
        );
    }
}
export default Index