import React, {Component} from "react";
import {Link} from "react-router-dom";
import axios from "axios";
import qs from "qs"

import "./todo.css"

class Index extends Component{
     state={
         token:window.localStorage.getItem("token"),
         pagelist:[],
         page:'',        //当前页码 默认1
         item_count:'',  //总计量数
         page_size:'',   //每页记录数
         dataList:[],
         id:'',
         name:'',
         status: '',     //状态
         created_at:'',
         updated_at: "",
         remind_at:'',
         finish_at:'',
         sortby:'finish_at',      //排序字段:id finish_at
         order:'asc',        //排序方式: asc升 desc降
         flag:'-1'
    }

    componentDidMount(){
         this.getData();
    }
    getData (){
        let token = window.localStorage.getItem("token")

        axios.get( 'https://www.it266.com/app/todo?token='+this.state.token,qs.stringify(this.state))
            .then((response)=>{
                // console.log(response)
                if(response.data.status){
                    this.setState({
                        dataList:response.data.data.data,
                        item_count:response.data.data.page.item_count,
                        page:response.data.data.page.page,
                        page_size:response.data.data.page.page_size,

                        status :response.data.data.data.status,
                        sortby :response.data.data.data.sortby,
                        order :response.data.data.data.order
                    })
                }
                else{
                    alert(response.data.data)
                }
            }).catch((error)=>{
                console.log(error)
            })

    }

    deleteTodo = (id) =>{
        let token = window.localStorage.getItem("token")
        console.log(id)
        let data ={
            id: id
        }
        axios.post('https://www.it266.com/app/todo/delete?token='+token+"&id"+id,qs.stringify(data))
            .then((response) => {
                // console.log(response)
                if (!response.data.status){
                    alert("删除成功")
                    window.location.reload()
                }else {
                    alert(response.data.data)
                }
            })
            .catch((error)=>{
            })
    }


    render() {
        return(
            <div className='todo'>
                <div className="weui-flex one lines">
                    <div className="weui-flex__item"><div className="placeholder">待办</div></div>
                </div>
                <div className="weui-flex two lines">
                    <div className="weui-flex__item"><div className="placeholder">
                        <Link to={"/Data"}>全部任务({this.state.item_count})</Link>
                    </div></div>
                    <Link to="/Todo/create">新增</Link>
                </div>

                <div className="index">
                    {this.state.dataList.map((items) => {
                        return (
                            <div className="weui-flex lines" key={items.id}>
                                <div className="weui-cell weui-cell_active weui-cell_access">

                                    <div className="weui-cell__hd">
                                        <div className="index_top">
                                            <input type="text" value={items.status==1?'×':'√'} readOnly />
                                        </div>
                                    </div>

                                    <Link className="weui-cell index_middle" to={"/Todo/detail/"+items.id}>
                                        <div className="weui-cell__bd">
                                            <p>{items.name}</p>
                                        </div>
                                        <div className="weui-cell__ft">{items.created_at}</div>
                                    </Link>

                                    <div className="index_right">
                                        <Link className="link" to={"/Todo/update/"+items.id}>编辑</Link>
                                        <Link className="link" to="#" onClick={()=> {
                                            this.deleteTodo(items.id)
                                        }}>删除</Link>
                                    </div>
                                </div>
                            </div>
                            )
                        }
                        )}
                </div>

            </div>
        )
    }
}
export default Index