import React,{Component} from "react";
import axios from "axios";
import qs from "qs"
import "./todo.css"
import {Link} from "react-router-dom";
class Detail extends Component{

    state = {
        id: "1",
        name: "",
        remind_at: "",
        finish_at: "null",
        repeat: "",
        status: "",
        created_at: "",
        updated_at: ""
    }

    componentDidMount() {
        //匹配props中的id
        let id = this.props.match.params.id;

        //把对应id的文章数据,从api获取到,放到表单中,作为默认数据
        let token = window.localStorage.getItem("token")
        axios.get( 'https://www.it266.com/app/todo/detail?token='+token+'&id='+id)
            .then((response)=>{
                // console.log(response)
                if(!response.data.status){
                    alert(response.data['0'])
                }else {
                    this.setState({id:response.data.data.id})
                    this.setState({name:response.data.data.name})
                    this.setState({repeat:response.data.data.repeat})
                    this.setState({status:response.data.data.status})
                    this.setState({remind_at:response.data.data.remind_at})
                    this.setState({created_at:response.data.data.created_at})
                    this.setState({updated_at:response.data.data.updated_at})
                }
            })
    }

    render() {
        return(
            <div className="detail">
                <div className="weui-flex one lines">
                    <div className="weui-flex__item"><div className="placeholder">详情待办</div></div>
                </div>

                <div className="weui-flex two lines">
                    <div className="weui-flex__item"><div className="placeholder"></div></div>
                    <Link to={"/Todo"} className="success"><div className="placeholder">完成</div></Link>
                </div>

                <div className="weui-flex lines three">
                    <div className="weui-flex__item"><div className="placeholder three_lc">ID :</div></div>
                    <div className="weui-flex__item"><div className="placeholder">{this.state.id}</div></div>
                </div>
                <div className="weui-flex lines three">
                    <div className="weui-flex__item"><div className="placeholder three_lc">标题 :</div></div>
                    <div className="weui-flex__item"><div className="placeholder">{this.state.name}</div></div>
                </div>
                <div className="weui-flex lines three">
                    <div className="weui-flex__item"><div className="placeholder three_lc">状态 :</div></div>
                    <div className="weui-flex__item"><div className="placeholder">{this.state.status ==1?'未完成':'已完成'}</div></div>
                </div>
                <div className="weui-flex lines three">
                    <div className="weui-flex__item"><div className="placeholder three_lc">重复 :</div></div>
                    <div className="weui-flex__item"><div className="placeholder">
                        {this.state.repeat==0?'重复':
                            this.state.repeat==1?'每天':
                            this.state.repeat==2?'每周':
                            this.state.repeat==3?'每月':
                            this.state.repeat==2?'每季度':'每年'}
                    </div></div>
                </div>
                <div className="weui-flex lines three">
                    <div className="weui-flex__item"><div className="placeholder three_lc">提醒时间 :</div></div>
                    <div className="weui-flex__item"><div className="placeholder">{this.state.remind_at}</div></div>
                </div>
                <div className="weui-flex lines three">
                    <div className="weui-flex__item"><div className="placeholder three_lc">创建时间 :</div></div>
                    <div className="weui-flex__item"><div className="placeholder">{this.state.created_at}</div></div>
                </div>
                <div className="weui-flex lines three">
                    <div className="weui-flex__item"><div className="placeholder three_lc">更新时间 :</div></div>
                    <div className="weui-flex__item"><div className="placeholder">{this.state.updated_at}</div></div>
                </div>

            </div>
        )
    }
}
export default Detail