import React,{Component} from "react";
import axios from "axios";
import qs from "qs"
import "./todo.css"
import {Link} from "react-router-dom";

class Update extends Component{

    state = {
        id: "",
        name: "",
        remind_at: "",
        status: "",
        repeat: "",
        created_at: "",
        updated_at: ""
    }

    componentDidMount() {
        //匹配props中的id
        let id = this.props.match.params.id;

        //把对应id的文章数据,从api获取到,放到表单中,作为默认数据
        let token = window.localStorage.getItem("token")
        axios.get( 'https://www.it266.com/app/todo/detail?token='+token+'&id='+id)
            .then((response)=>{
                // console.log(response)
                if(!response.data.status){
                    alert(response.data['0'])
                }else {
                    this.setState({id:response.data.data.id})
                    this.setState({name:response.data.data.name})
                    this.setState({status:response.data.data.status})
                    this.setState({repeat:response.data.data.repeat})
                    this.setState({remind_at:response.data.data.remind_at})
                    this.setState({created_at:response.data.data.created_at})
                    this.setState({updated_at:response.data.data.updated_at})
                }
            })
    }


    handleClick = () => {
        //this.state 修改后的数据
        let token = window.localStorage.getItem("token")
        // console.log(this.state.status)
        //ajax 成功听到列表
        axios.post('https://www.it266.com/app/todo/update?token='+token,qs.stringify(this.state))
            .then((response)=>{
                if(response.data.status){
                // console.log(response)
                    this.props.history.push("/Todo")
                }else{
                    alert(response.data.data)
                }
            })
    }

    HandleInputId = (e) =>{
        console.log(this.state);
        this.setState({id:e.target.value})
    }
    HandleInputName = (e) => {
        this.setState({name:e.target.value})
    }
    HandleInputStatus = (e) => {
        this.setState({status:e.target.value})
    }
    HandleInputRepeat = (e) => {
        this.setState({repeat:e.target.value})
    }
    HandleInputRemind = (e) => {
        this.setState({remind_at:e.target.value})
    }

    render() {
        return(
            <div className="update">
                <div className="weui-flex one lines">
                    <div className="weui-flex__item"><div className="placeholder">待办详情</div></div>
                </div>

                <div className="weui-flex two lines">
                    <div className="weui-flex__item"><div className="placeholder"></div></div>
                    <Link to={"/Todo"} className="success"><div className="placeholder" onClick={this.handleClick}>完成</div></Link>
                </div>

                <div className="weui-flex lines three">
                    <div className="weui-flex__item"><div className="placeholder three_lc">ID :</div></div>
                    <div className="weui-flex__item"><div className="placeholder">
                        <input readOnly type="text" value={this.state.id||""} onChange={this.HandleInputId}/>
                    </div></div>
                </div>
                <div className="weui-flex lines three">
                    <div className="weui-flex__item"><div className="placeholder three_lc">标题 :</div></div>
                    <div className="weui-flex__item"><div className="placeholder">
                        <input type="text" value={this.state.name} onChange={this.HandleInputName}/>
                    </div></div>
                </div>
                <div className="weui-flex lines three">
                    <div className="weui-flex__item"><div className="placeholder three_lc">状态 :</div></div>
                    <div className="weui-flex__item"><div className="placeholder">
                        <input readOnly type="text" value={this.state.status} onChange={this.HandleInputStatus}/>
                    </div></div>
                </div>
                <div className="weui-flex lines three">
                    <div className="weui-flex__item"><div className="placeholder three_lc">重复 :</div></div>
                    <div className="weui-flex__item"><div className="placeholder">
                       <input type="text" value={this.state.repeat} onChange={this.HandleInputRepeat}/>
                    </div></div>
                </div>
                <div className="weui-flex lines three">
                    <div className="weui-flex__item"><div className="placeholder three_lc">提醒时间 :</div></div>
                    <div className="weui-flex__item"><div className="placeholder">
                        <input type="text" value={this.state.remind_at||""} onChange={this.HandleInputRemind}/>
                    </div></div>
                </div>
                <div className="weui-flex lines three">
                    <div className="weui-flex__item"><div className="placeholder three_lc">创建时间 :</div></div>
                    <div className="weui-flex__item"><div className="placeholder">
                        <input readOnly value={this.state.created_at}/>
                    </div></div>
                </div>
                <div className="weui-flex lines three">
                    <div className="weui-flex__item"><div className="placeholder three_lc">更新时间 :</div></div>
                    <div className="weui-flex__item"><div className="placeholder">
                        <input readOnly value={this.state.updated_at}/>
                    </div></div>
                </div>
            </div>
        )
    }
}
export default Update